import uldir from "./uldir.js";

const raids = {
    "uldir": uldir
}

export function getRaid(id, cb) {
    id = id.toLowerCase();
    if (raids[id]) {
        cb(null, raids[id])
    } else {
        cb(new Error('Boss not found'))
    }
}

export function getBoss(raidId, bossId, cb) {
    raidId = raidId.toLowerCase();
    bossId = bossId.toLowerCase();
    var raid = raids[raidId];
    if (raid) {
        var boss = raid.bosses[bossId];
        if (boss) {
            cb(null, boss)
        } else {
            cb(new Error('Boss not found'))
        }
    } else {
        cb(new Error('Raid not found'))
    }
}